import React from 'react'
import { withRouter } from "react-router";
import Appbar from '../appbar/Appbar'

function Home(props) {
    React.useEffect(() => {
       const login = localStorage.getItem("login")
       if(!login){
           props.history.push("/admin/login")
       }
    }, [])
    return (
        <div>
            <Appbar />
        </div>
    )
}
export default withRouter(Home);