import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { withRouter } from "react-router";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright Â© '}
      <Link color="inherit" href="https://material-ui.com/">
        Techgater
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

function SignIn(props) {
  const classes = useStyles();
  const [state, setstate] = React.useState({
      email:"",
      password:"",
      error:false,
      message:"",
  })

  const onChange = (prop) => (e) =>{
    setstate({...state,[prop]:e.target.value})
  }

  const LoginData = {
    email:"kamran@techgater.com",
    password:"Rattokala@786",
    }

    React.useEffect(() => {
        const login = localStorage.getItem("login")
        if(login){
            props.history.push("/admin")
        }
     }, [])

    const validate = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

    const onSubmit=(e)=>{
        e.preventDefault()
        if(state.email.match(validate) && state.password.length > 5){
            if(state.email !== LoginData.email){
                setstate({...state,error:true,message:"Please enter a valid Email Address!"})
                if(state.password !== LoginData.password){
                    setstate({...state,error:true,message:"Password is not correct!"})
                }
            }
            
            else{
                setstate({...state,error:false,message:""})
                localStorage.setItem("login",true);
                props.history.push("/admin")
            }
        }

    }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      {
          state.error && <Box textAlign="center" color="red" fontSize={20} mb={10}>{state.message}</Box>
      }
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            onChange={onChange("email")}
            id="email"
            helperText={!state.email.match(validate) && "Please enter valid email address"}
            label="Email Address"
            name="email"
            autoComplete="email"
            error={!state.email.match(validate)}
            autoFocus
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            error={state.password.length < 5}
            helperText={state.password.length < 5 && "Password should contains 6 charts"}
            fullWidth
            onChange={onChange("password")}
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
          />
          <Button
            fullWidth
            onClick={onSubmit}
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign In
          </Button>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
}
export default withRouter(SignIn)