import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
import Login from '../login/Login'
import Home from '../home/Home'

export default function Routes() {
    
    return (
        <Router>
            <Switch>
            <Route path="/admin/login">
                <Login/> 
            </Route>
            <Route path="/admin">
                <Home/> 
            </Route>
        </Switch>
        </Router>
    )
}
